import sampleSize  from 'lodash.samplesize';
// import sample   from 'lodash.sample';
export  function xu_ly_content (data_out){
  var rs=[];
  data_out.key_words_TKC.forEach((e)=> {
    let title= sampleSize(data_out.titles,1)[0].title;
    if(title.search("tu_khoa_chinh")>-1) title=fs_replace_all(title,'tu_khoa_chinh',e);
    let short_des=sampleSize(data_out.short_des,1)[0].mt;
    if(short_des.search("tu_khoa_chinh")>-1) short_des=fs_replace_all(short_des,'tu_khoa_chinh',e);
    Object.keys(data_out.list_content).forEach((key)=>{
      title=fs_replace_all(title,key,sampleSize(data_out.list_content[key],1)[0]);
      while(short_des.search(key)>-1){
        let a=sampleSize(data_out.list_content[key],1)[0];
        short_des=short_des.replace(key,a)
      }
    })
    title=title[0].toUpperCase()+ title.slice(1);
    short_des=short_des[0].toUpperCase()+ short_des.slice(1);
      rs.push({
        title:title,
        short_des:short_des,
        thumnail:'',
        table_price:{
          type_title:'Kích thước',
          og_price_title:'Giá gốc',
          sale_price_title:'Giá bán',
          table:[]
        },
        data_trick:{
          is_show:false,
          price:0,
          sale:0,
          kt:''
        },
      })
  })
  return rs;
}
function fs_replace_all(text,find,replace){
  return text.replace(new RegExp(find, 'g'), replace);
}
 
