import React, { Component } from 'react';
import '../admin/admin.css'
import '../index.css'
import './index.css'
import '../App.css'
// import Posts from './posts'
// import Pages from './pages'
// import Categorys from './categorys'
import Setups from './components/setup'
import Orders from './components/orders'
import Pages from './components/Pages'
import Tools from './components/tools'
import Google_shoping_sheet from './components/Google_shoping_sheet';
export default class Index_qc extends Component {
  constructor (props) {
    super(props)
    this.state = {
      seleted:'orders'
    }
  }
  render() {
      let {seleted}=this.state;
    return (
      <React.Fragment>
        
        <div>
          <div className=''>
            <div className=' navbarz'>
              <ul className='menuz'>
                <li style={{textAlign:"center",fontSize:"32px"}} onClick={()=>window.location.href = window.url+"/wp-admin/"}><a  className="homexz"><i className="fa-solid fa-house-user"></i></a></li>
                <li onClick={()=>this.setState({seleted:'orders'})}  className={seleted==='orders'?'active':''}><i className="fa-solid fa-cart-shopping"></i> <span className="hgfs"> Đơn hàng - Bình Luận</span></li>
                {/* <li onClick={()=>this.setState({seleted:'categorys'})}  className={seleted==='categorys'?'active':''}><i className="fa-brands fa-battle-net"></i> <span className="hgfs"> Danh mục </span></li> */}
                <li onClick={()=>this.setState({seleted:'pages'})}  className={seleted==='pages'?'active':''}><i className="fa-solid fa-file-word"></i> <span className="hgfs">Bài quảng cáo</span></li>
                <li onClick={()=>this.setState({seleted:'setups'})}  className={seleted==='setups'?'active':''}><i className="fa-solid fa-gear"></i> <span className="hgfs"> Cài đặt</span></li>
                <li onClick={()=>this.setState({seleted:'tools'})}  className={seleted==='tools'?'active':''}><i className="fa-solid fa-signs-post"></i> <span className="hgfs"> Tools Google Shoping</span></li>
                <li onClick={()=>this.setState({seleted:'Google_shoping_sheet'})}  className={seleted==='Google_shoping_sheet'?'active':''}><i className="fa-solid fa-table"></i> <span className="hgfs"> Google shoping sheet</span></li>
              
              </ul>
            </div>
            <div className=' csdf'>
              {seleted==='tools'&&<Tools />}
              {seleted==='pages'&&<Pages />}
              {seleted==='Google_shoping_sheet'&&<Google_shoping_sheet />}
              {seleted==='setups'&&<Setups />}
              {seleted==='orders'&&<Orders />}
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

