import React, { Component } from 'react';
import Table from 'react-bootstrap/Table';
import Media_img from './lib/media/Add_media'
import { toast } from 'react-toastify';
import {
  get_comment_admin,
  update_comment,
  delete__comment_by_id
} from './lib/axios'
const quality=6;
export default class Comments extends Component {
  constructor (props) {
    super(props)
    this.state = {
      media_pic:{
        is_show:false,
        index:-1
      },
      page:1,
      show_more:false,
      data_edit:{
        index:-1,
        data:{
          id:-1,
          time:'DEC 24, 2022',
          data:{
            id_catch:'',
            id_comment:'',
            status:'publish',
            content:'',
            author:'',
            phone:'0963226771',
            img:[],
            type:'nl',
            rep:{
              author:'Admin',
              contents:'',
            }
          }
        }
      },
      comments:[
        // {
        //   id:1,
        //   time:'DEC 24, 2022',
        //    is_set:'0',
        //   data:{
        //  url_ref:'',
        //      id_comment:'asdasda',
        //     status:'private',
        //     content:'Shop dễ thương,hỗ trợ nhiệt tình,giá siêu tốt,đặt chiều qa ság nay đã nhận đc lun.Vừa kịp thuốc hết.Bé đã dùg đc 2 hũ r,bé thích vị dâu của thuốc dễ uốg.Đây là 1 lựa chọn tốt dành cho các mẹ có con bị DUDB như e, vừa bổ sung d3&canxi ko fai uốg lắc nhắc khỏi qên.Ủg hộ dài lâu.cho 5k sao cũg đc lun🤩',
        //     author:'Thành Danh',
        //     phone:'0963226771',
        //     type:'nl',
        //     img:[
        //       "https://cofa.vn/wp-content/uploads/2022/12/giuong-sat-AB21x.jpg",
        //       "https://cofa.vn/wp-content/uploads/2022/12/giuong-ngu-hoang-gia-AB123.jpg",
        //       "https://cofa.vn/wp-content/uploads/2022/12/giuong-ngu-1m8-AB123.jpg",
        //       "https://cofa.vn/wp-content/uploads/2022/12/giuong-ngu-hien-dai-AB123.jpg"
        //     ],
        //     rep:{
        //       author:'Admin',
        //       contents:'Shop xin cảm ơn bạn rất nhiều!',
        //     }
        //   }
        // },
        // {
        //   id:2,
        //   time:'DEC 23, 2022',
        //    is_set:'0',
        //   data:{
        //    url_ref:'',
        //      id_comment:'asdasda',
        //     status:'publish',
        //     content:'Shop dễ thương,hỗ trợ nhiệt tình,giá siêu tốt,đặt chiều qa ság nay đã nhận đc lun.Vừa kịp thuốc hết.Bé đã dùg đc 2 hũ r,bé thích vị dâu của thuốc dễ uốg.Đây là 1 lựa chọn tốt dành cho các mẹ có con bị DUDB như e, vừa bổ sung d3&canxi ko fai uốg lắc nhắc khỏi qên.Ủg hộ dài lâu.cho 5k sao cũg đc lun🤩',
        //     author:'Ngọc Thảo',
        //     phone:'0963226771',
        //     img:[],
        //     type:'nl',
        //     rep:{
        //       author:'Admin',
        //       contents:'',
        //     }
        //   }
        // },
      ]
    }
  }
  async componentDidMount(){
    let comments=await get_comment_admin(0);
    if(comments.length>0){
      let show_more=true;
      if(comments.length<quality) show_more=false;
      this.setState({comments:comments,show_more:show_more})
    }else{
      this.setState({show_more:false})
    }
  }
  // 

  render() {
      let {comments,data_edit,media_pic, page} =this.state;
      console.log("🚀 ~ file: comments.js:103 ~ render ~ comments:", comments)
    return (
      <React.Fragment>

        <Table striped bordered hover >
            <thead>
            <tr>
                <th style={{width:'1%'}}>#</th>
                <th style={{width:'10%'}}>Thời gian</th>
                <th style={{width:'10%'}}>Tên</th>
                <th style={{width:'15%'}}>Phone</th>
                <th style={{width:'80%'}}>Bình Luận</th>
                <th style={{width:'9%'}}>Xóa</th>
            </tr>
            </thead>
            <tbody>
            {
                comments.map((e,i)=>{
                    let time_his = Date.parse(e.time);
                    let time_now= new Date().getTime();
                    let time_long=Math.floor((time_now-time_his)/60000);//all phut
                    let count_day=Math.floor(time_long/1440);//
                    let count_hour=Math.floor(time_long/60)-count_day*24;//
                    let count_Minutes=time_long-count_day*24*60-count_hour*60;
                    let show_time='';
                    if(count_day>0){
                        show_time+=count_day+" ngày ";
                    }
                    if(count_hour>0){
                        show_time+=count_hour+" giờ ";
                    }
                    show_time+=count_Minutes+ " phút trước";
        
                    return(
                    <tr  key={e.id}>
                        <td>
                            <a href={e.data.url_ref} target="_blank"
                              style={{fontSize:"10px"}}
                            >Xem</a>
                        </td>
                        <td className={time_long<1440?'vnd chz':'vnd'}>
                          {show_time}
                        </td>
                        <td  className="uukk">
                          {e.data.author}
                        </td>
                        <td style={{fontSize:"13px"}}>
                          {e.data.phone}<br/>
                          {e.data.status=="publish"&&<span className='pub'>Publish</span>}
                          {e.data.status=="private"&&<span className='pri'>Private</span>}
                        </td>
                        <td style={{fontSize:"14px"}}>
                          {e.data.type=="nl"&&<div>
                            <i className="fa-solid fa-user-check" style={{color:"#446cb3",marginRight:"5px"}}></i>
                            <i style={{color:"#446cb3"}}>Người ghé thăm</i>
                          </div>}
                          {e.data.type=="nm"&&<div>
                            <i className="fa-solid fa-circle-check" style={{color:"#52b858",marginRight:"5px"}}></i>
                            <i style={{color:"#52b858"}}>Đã mua hàng</i>
                          </div>}
                          <div className='rela'>
                            {e.data.content}
                            <div className='imhs'>
                              {
                                e.data.img.map((u,i)=>{
                                  return <img key={i} src={u} className="nonozs"/>
                                })
                              }
                            </div>
                            <div className='repzds'>
                              <a className='reps'
                                onClick={()=>{
                                  this.setState({
                                    data_edit:{
                                      index:i,
                                      data:{
                                        id:e.id,
                                        data:{
                                          url_ref:e.data.url_ref,
                                          id_catch:e.data.id_catch,
                                          id_comment:e.data.id_comment,
                                          status:e.data.status,
                                          content:e.data.content,
                                          author:e.data.author,
                                          phone:e.data.phone,
                                          img:e.data.img,
                                          type:e.data.type,
                                          rep:{
                                            author:e.data.rep.author,
                                            contents:e.data.rep.contents,
                                          }
                                      }
                                      }
                                    }
                                  })
                                }}
                              >Chỉnh sửa</a>
                              {e.data.rep.contents!=''&&<div className='admin-rep'>
                                <b>{e.data.rep.author}</b>
                                <div className='xasdas'>{e.data.rep.contents}</div>
                              </div>}
                            </div>
                            {data_edit.index==i&&<div className='edit-comment'>
                            <div className='rela'>
                                <div>
                                  <div className='row'>
                                    <div className='col-2'>Tên khách</div>
                                    <div className='col-10'>
                                      <input type="text" className='name-comment'
                                        value={data_edit.data.data.author}
                                        onChange={(e)=>{
                                          let {data_edit}=this.state;
                                          data_edit.data.data.author=e.target.value
                                          this.setState({data_edit:data_edit})
                                        }}
                                       />
                                    </div>
                                    <div className='row'>
                                      <div className='col-4'>
                                        <input type="checkbox" 
                                          checked={data_edit.data.data.type=="nm"?true:false} 
                                          onChange={()=>{
                                            let {data_edit}=this.state;
                                            data_edit.data.data.type="nm";
                                            this.setState({data_edit:data_edit})
                                          }}
                                        />
                                        <i style={{color:"rgb(16 126 23)"}}>Đã mua hàng</i>
                                      </div>
                                      <div className='col-4'>
                                        <input type="checkbox" 
                                          checked={data_edit.data.data.type=="nl"?true:false}
                                          onChange={()=>{
                                            let {data_edit}=this.state;
                                            data_edit.data.data.type="nl";
                                            this.setState({data_edit:data_edit})
                                          }}
                                        />
                                        <i style={{color:"#446cb3"}} >Người ghé thăm</i>
                                      </div>
                                      <div className='col-4'>
                                        <input type="checkbox" 
                                          checked={data_edit.data.data.type=="nb"?true:false}
                                          onChange={()=>{
                                            let {data_edit}=this.state;
                                            data_edit.data.data.type="nb";
                                            this.setState({data_edit:data_edit})
                                          }}
                                        />
                                        <i style={{color:"#446cb3"}} >Người Bán</i>
                                      </div>
                                      
                                    </div>
                                    <div className='col-2'>Khách Bình Luận</div>
                                    <div className='col-8'>
                                      <textarea className='comsx'
                                        value={data_edit.data.data.content}
                                        onChange={(e)=>{
                                          let {data_edit}=this.state;
                                          data_edit.data.data.content=e.target.value
                                          this.setState({data_edit:data_edit})
                                        }}
                                      />
                                    </div>
                                    <div className='col-12'>
                                      {
                                        data_edit.data.data.img.map((u,k)=>{
                                          return <div key={u} className="wrts">
                                            <img  src={u} className="nonozs"/>
                                            <i className="fa-solid fa-circle-xmark toxxv"
                                              onClick={()=>{
                                                let {data_edit}=this.state;
                                                data_edit.data.data.img.splice(k,1)
                                                this.setState({data_edit:data_edit})
                                              }}
                                            ></i>
                                          </div>
                                        })
                                      }
                                    <a className='reps'
                                      onClick={()=>this.setState({media_pic:{is_show:true,index:i}})}
                                    >Thêm hình</a>
                                    </div>
                                  </div>
                                </div>
                                <div>
                                  <div className='row'>
                                    <div className='col-2'>Tên Admin</div>
                                    <div className='col-10'>
                                      <input type="text" className='name-comment'
                                        value={data_edit.data.data.rep.author}
                                        onChange={(e)=>{
                                          let {data_edit}=this.state;
                                          data_edit.data.data.rep.author=e.target.value
                                          this.setState({data_edit:data_edit})
                                        }}
                                      />
                                    </div>
                                    <div className='col-2'>Admin trả lời</div>
                                    <div className='col-8'>
                                      <textarea  className='comsx'
                                        value={data_edit.data.data.rep.contents}
                                        onChange={(e)=>{
                                          let {data_edit}=this.state;
                                          data_edit.data.data.rep.contents=e.target.value
                                          this.setState({data_edit:data_edit})
                                        }}
                                      />
                                    </div>
                                  </div>
                                </div>
                                <div className='w-pp'>
                                  <span className={data_edit.data.data.status=="publish"?'w-pu pub':'w-pu pub de-active'}
                                    onClick={()=>{
                                      let {data_edit}=this.state;
                                        data_edit.data.data.status='publish'
                                        this.setState({data_edit:data_edit})
                                    }}
                                  >Publish {data_edit.data.data.status=="publish"&&<i className="fa-solid fa-check"></i>}</span>
                                  <span className={data_edit.data.data.status=="private"?'w-pr pri':'w-pr pri de-active'}
                                  onClick={()=>{
                                    let {data_edit}=this.state;
                                      data_edit.data.data.status='private'
                                      this.setState({data_edit:data_edit})
                                  }}
                                  >Private {data_edit.data.data.status=="private"&&<i className="fa-solid fa-check"></i>}</span>
                                </div>
                                <span className='ok-comment'
                                    onClick={async()=>{
                                      let {comments,data_edit}=this.state;
                                      let data_send={
                                        is_set:e.is_set,
                                        id:data_edit.data.id,
                                        status:data_edit.data.data.status,
                                        phone:data_edit.data.data.phone,
                                        id_comment:data_edit.data.data.id_comment,
                                        value_comment:JSON.stringify({
                                          url_ref:e.data.url_ref,
                                          id_catch:data_edit.data.data.id_catch,
                                          content:data_edit.data.data.content,
                                          author:data_edit.data.data.author,
                                          phone:data_edit.data.data.phone,
                                          type:data_edit.data.data.type,
                                          img:data_edit.data.data.img,
                                          rep:{
                                            author:data_edit.data.data.rep.author,
                                            contents:data_edit.data.data.rep.contents,
                                          }
                                        })

                                      }
                                      console.log("🚀 ~ file: comments.js:346 ~ Comments ~ onClick={async ~ data_send", data_send)
                                      let a=await update_comment(data_send);
                                      if(a.status){
                                        toast.success('Chỉnh sửa thành công',{theme: "colored"})
                                        // // ok moi den buoc nay
                                        comments[data_edit.index]=data_edit.data;
                                        comments[data_edit.index].is_set=a.is_set;
                                        this.setState({
                                          data_edit:{
                                            index:-1,
                                            data:e
                                          },
                                          comments:comments
                                        })
                                      }

                                    }}
                                >ok</span>
                                <span className='x-comment'
                                onClick={()=>{
                                  this.setState({
                                    data_edit:{
                                      index:-1,
                                      data:e
                                    }
                                  })
                                }}
                                >Hủy</span>
                              </div>
                            </div>}
                          </div>
                        </td>
                        <td>
                          <div className='delete iconh'
                              onClick={async()=>{ 
                                if (window.confirm(`Xóa bình luận này : ${e.data.author}`)) {
                                  let {comments}=this.state;
                                   let a=await delete__comment_by_id(e.id,e.data.status,e.data.id_comment);
                                   if(a){
                                    comments.splice(i,1);
                                    this.setState({comments:comments})
                                    toast.success(`Xóa thành công.`,{theme: "colored"})
                                   }else{
                                    toast.error('Xóa không thành công.',{theme: "colored"})
                                   }
                               
                                } 
                              }}
                          ><span>Xóa</span></div>
                         
                        </td> 
                      
                    </tr>
                    )
                })
            }

            </tbody>
        </Table>
        {this.state.show_more&&<div className='morexz'
          onClick={async()=>{
            let {page,comments}=this.state;
            let comment_new=await get_comment_admin(page);
            if(comment_new.length>0){
              let show_more=true;
              if(comment_new.length<quality) show_more=false;
              let cm=[...comments,...comment_new]
              this.setState({comments:cm,show_more:show_more,page:page+1})
            }else{
              this.setState({show_more:false})
            }
          }}
        ><span>Xem thêm</span></div>}
        {data_edit.index!==-1&&<div className='dimmer-c'></div>}
        <Media_img 
          is_open_media={media_pic.is_show}
          handleClose={()=>this.setState({media_pic:{is_show:false,index:true}})}
          fsReturn_media={(img_return)=>{
            let {data_edit}=this.state;
            data_edit.data.data.img.push(img_return.img);
            this.setState({
              media_pic:{is_show:false,index:true},
              data_edit:data_edit
            })
          }}
        />
      </React.Fragment>
    );
  }
}

