  //
  async componentDidMount(){
    let {type,post_selected}=this.state;
    if(type=="create"){
      let qc_goc=await get_categorys_or_pages(-1);
      this.setState({qc_goc:qc_goc})
    }else{
      let data=await get_page_info(post_selected.id);
      data.long_des=data.long_des==undefined?"":data.long_des
      data.gia_tri_chuyen_doi=data.gia_tri_chuyen_doi==undefined?1:data.gia_tri_chuyen_doi
      // convert khuyen mai
      for(let i=0;i<data.data_qc.dm.length;i++){
        for(let j=0;j<data.data_qc.dm[i].list_sp.length;j++){
          if(data.data_qc.dm[i].list_sp[j].km==undefined){
            data.data_qc.dm[i].list_sp[j].km={
              is_km:false,
              title:'🍀Combo khuyến mãi👉có 1 không 2🍀',
              img:'',
              timer_run:{
                amp:0,
                d:0,
                h:0,
                m:0,
                s:0
              },
              timer_lock:{
                amp:0,
                d:0,
                h:0,
                m:0,
                s:0
              },
              des_scroll:'',
              note:'Nếu quý khách hàng không nhận sản phẩm tặng thêm, chúng tôi sẽ khấu trừ khoản tiền bằng 1 nửa tiền giá trị sản phẩm được tặng.',
              data_km:[]
            }
          }
        }
      }
      //
      if(data!=false){
      this.setState({data:data})
      }
    }
  }
  //
  static getDerivedStateFromProps(nextProps, prevState){
    if(nextProps.trigerE!==prevState.trigerE){
      if(nextProps.post_selected.type==="create"){
        return {
          trigerE:nextProps.trigerE,
          type:nextProps.post_selected.type,
        }
      }else{
        return {
          trigerE:nextProps.trigerE,
          type:nextProps.post_selected.type,
          post_selected:nextProps.post_selected,
        }
      }
   }
    return null;
  }
  //
  fs_create_update_post=async ()=>{
    let {data,type,post_selected}=this.state;
    let id=type=="edit"?post_selected.id:-1;
    data.top=this.get_top_position(data.data_qc.dm);
    data.data_qc.dm=data.id_parent!=-1?[]:data.data_qc.dm;
    data.long_des=data.long_des.replace(new RegExp('"', 'g'), "'");
    data.thumnail_dt=this.convert_thumnail_dt(data.data_qc.dm)
    let rs={
        id:id,
        title:data.title,
        content:data.long_des,
        status:data.status?'publish':'private',
        id_parent:data.id_parent,
        metaA:JSON.stringify(data)
      }

    let a=await action_create_or_edit_page(rs);

      if(a.id!=undefined){
        toast.success(type=="edit"?'Chỉnh sửa thành công':'Tạo mới thành công',{theme: "colored"})
        this.props.dataUpdate_list_posts(a);
      }else{
        toast.error(type=="edit"?'Chỉnh sửa thất bại':'Tạo mới thất bại',{theme: "colored"})
      }


  }
